import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { MarketProvider } from '../../providers/market/market';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginProvider } from '../../providers/login/login';

@IonicPage()
@Component({
  selector: 'page-syarat',
  templateUrl: 'syarat.html',
})
export class SyaratPage {
  private syarat:any;
  private clientId: any;
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public mkt: MarketProvider,
    public lgn: LoginProvider,
    public tools: ToolsProvider,
    public platform: Platform
  ) {
    this.getSK();
  }

  ionViewDidLoad(){
    this.platform.ready().then(()=>{
      this.checkLogin();
    });
  }
  
  checkLogin(){
    this.tools.getStg('login').then(
      (data)=>{
        if(!data || data.level !== '0'){
          this.navCtrl.setRoot('LoginPage');
        }else{
          this.clientId = data.clientId;
          this.tools.getStg('ip').then(
            (data)=>{
              if(data){
                this.lgn.logger(this.clientId,'Masuk halaman Syarat & Ketentuan', data.ip).subscribe();
                this.tools.getStg('sdkt').then(
                  res=>{
                    if(res){
                      // console.log('res=>',res);
                      this.syarat = res;
                    }else{
                      this.getSK();
                    }
                  },err=>{
                    this.getSK();
                  }
                );
              }
            }
          );
        }
      },
      err=>{
        console.log('History-> getStgLogin', err);
      }
    );
  }

  getSK(){
    this.mkt.getAturanWeb().subscribe(
      data=>{
        // console.log(data);
        this.tools.setStg('sdkt',data);
        this.syarat = data;
      }
    );
  }
}



